<script>
export default {
  data(){
      return{
          restaurantes: this.$store.state.restaurants,
          discount_x: true,
          limit: 8,
          scrollTop: 0,
          height: 0
      }
  },
  methods: {
        discountA(index){
            this.discount_x =  (this.$store.state.restaurants[index].discount_type === 1) ? true : false;
            return this.discount_x;
        },
        showMore(){
            this.limit +=3;
            this.scrollTop= 0;
            this.intervalId = setInterval(() => {
                if (this.scrollTop === 220) {
                clearInterval(this.intervalId);
                }else {
                    this.scrollTop += 20
                    window.scroll(0, window.pageYOffset + 20);
                }
                
            }, 20)
        },
          
        hide(){
            this.limit = 8;
            this.intervalId = setInterval(() => {
                if (window.pageYOffset === 0) {
                clearInterval(this.intervalId);
                }else {
                    window.scroll(0, window.pageYOffset - 40);
                }
                
            }, 20)
        },
  }
}
</script>
